import { useState } from 'react'

import { ChevronLeft as PrevIcon, ChevronRight as NextIcon } from '@mui/icons-material'
import { Box, IconButton, Typography, Paper, Stack } from '@mui/material'

interface CalendarProps {
  markedDates: Map<string, { count: number; color?: string }>
  onDateClick?: (date: Date) => void
}

const WEEKDAYS = ['일', '월', '화', '수', '목', '금', '토']

export function Calendar({ markedDates, onDateClick }: CalendarProps) {
  const [currentMonth, setCurrentMonth] = useState(() => {
    const now = new Date()
    return new Date(now.getFullYear(), now.getMonth(), 1)
  })

  const year = currentMonth.getFullYear()
  const month = currentMonth.getMonth()

  const firstDayOfMonth = new Date(year, month, 1).getDay()
  const daysInMonth = new Date(year, month + 1, 0).getDate()

  const prevMonth = () => {
    setCurrentMonth(new Date(year, month - 1, 1))
  }

  const nextMonth = () => {
    setCurrentMonth(new Date(year, month + 1, 1))
  }

  const days: (number | null)[] = []
  for (let i = 0; i < firstDayOfMonth; i++) {
    days.push(null)
  }
  for (let i = 1; i <= daysInMonth; i++) {
    days.push(i)
  }

  const today = new Date()
  const isToday = (day: number) =>
    today.getFullYear() === year && today.getMonth() === month && today.getDate() === day

  const getDateKey = (day: number) => {
    const date = new Date(year, month, day)
    return date.toISOString().split('T')[0]
  }

  return (
    <Paper sx={{ p: 2 }}>
      {/* Header */}
      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 2 }}>
        <IconButton onClick={prevMonth} size="small">
          <PrevIcon />
        </IconButton>
        <Typography variant="h6">
          {year}년 {month + 1}월
        </Typography>
        <IconButton onClick={nextMonth} size="small">
          <NextIcon />
        </IconButton>
      </Stack>

      {/* Weekday headers */}
      <Box
        sx={{
          display: 'grid',
          gridTemplateColumns: 'repeat(7, 1fr)',
          gap: 0.5,
          mb: 1,
        }}
      >
        {WEEKDAYS.map((day, index) => (
          <Typography
            key={day}
            variant="caption"
            align="center"
            sx={{
              color: index === 0 ? 'error.main' : index === 6 ? 'primary.main' : 'text.secondary',
              fontWeight: 'bold',
            }}
          >
            {day}
          </Typography>
        ))}
      </Box>

      {/* Calendar grid */}
      <Box
        sx={{
          display: 'grid',
          gridTemplateColumns: 'repeat(7, 1fr)',
          gap: 0.5,
        }}
      >
        {days.map((day, index) => {
          if (day === null) {
            return <Box key={`empty-${index}`} sx={{ aspectRatio: '1' }} />
          }

          const dateKey = getDateKey(day)
          const marked = markedDates.get(dateKey)
          const dayOfWeek = (firstDayOfMonth + day - 1) % 7

          return (
            <Box
              key={day}
              onClick={() => onDateClick?.(new Date(year, month, day))}
              sx={{
                aspectRatio: '1',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                borderRadius: 1,
                cursor: onDateClick ? 'pointer' : 'default',
                bgcolor: isToday(day) ? 'primary.light' : marked ? 'success.light' : 'transparent',
                color:
                  dayOfWeek === 0
                    ? 'error.main'
                    : dayOfWeek === 6
                      ? 'primary.main'
                      : 'text.primary',
                '&:hover': onDateClick
                  ? {
                      bgcolor: isToday(day)
                        ? 'primary.main'
                        : marked
                          ? 'success.main'
                          : 'action.hover',
                    }
                  : {},
              }}
            >
              <Typography variant="body2">{day}</Typography>
              {marked && (
                <Box
                  sx={{
                    width: 6,
                    height: 6,
                    borderRadius: '50%',
                    bgcolor: marked.color ?? 'success.dark',
                    mt: 0.25,
                  }}
                />
              )}
            </Box>
          )
        })}
      </Box>
    </Paper>
  )
}
