import { useEffect, useState } from 'react'

import { Link } from 'react-router-dom'

import { PlayArrow as PlayIcon } from '@mui/icons-material'
import {
  Container,
  Typography,
  Box,
  Paper,
  Stack,
  Card,
  CardContent,
  CardActions,
  Button,
  CircularProgress,
  Alert,
  LinearProgress,
  Grid,
} from '@mui/material'

import {
  getTrainingExecutions,
  getTrainingExecutionsByPlanId,
  calculateStats,
} from '@/api/trainingExecutions'
import { getTrainingPlans } from '@/api/trainingPlans'
import { Calendar } from '@/components/Calendar'
import { formatDuration } from '@/hooks/useTimer'
import { routes } from '@/routes'
import type { TrainingPlan, TrainingExecutionWithPlan } from '@/types/database'
import { calculateTotalSessions } from '@/utils/schedule'

interface PlanWithProgress extends TrainingPlan {
  completedSessions: number
  totalSessions: number
  progressPercent: number
}

export function DashboardPage() {
  const [plans, setPlans] = useState<PlanWithProgress[]>([])
  const [executions, setExecutions] = useState<TrainingExecutionWithPlan[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    loadData()
  }, [])

  async function loadData() {
    try {
      setLoading(true)
      const [plansData, executionsData] = await Promise.all([
        getTrainingPlans(),
        getTrainingExecutions(),
      ])

      // 각 계획별 진행률 계산
      const plansWithProgress = await Promise.all(
        plansData.map(async (plan) => {
          const planExecutions = await getTrainingExecutionsByPlanId(plan.id)
          const completedSessions = planExecutions.length
          const totalSessions = calculateTotalSessions(plan)
          const progressPercent = Math.min(100, (completedSessions / totalSessions) * 100)
          return {
            ...plan,
            completedSessions,
            totalSessions,
            progressPercent,
          }
        })
      )

      setPlans(plansWithProgress)
      setExecutions(executionsData)
    } catch (err) {
      setError('데이터를 불러오는데 실패했습니다.')
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <Container maxWidth="lg">
        <Box sx={{ display: 'flex', justifyContent: 'center', py: 8 }}>
          <CircularProgress />
        </Box>
      </Container>
    )
  }

  if (error) {
    return (
      <Container maxWidth="lg">
        <Box sx={{ my: 4 }}>
          <Alert severity="error">{error}</Alert>
        </Box>
      </Container>
    )
  }

  // 캘린더용 날짜별 훈련 횟수 계산
  const markedDates = new Map<string, { count: number }>()
  executions.forEach((execution) => {
    const dateKey = execution.completed_at.split('T')[0]
    const existing = markedDates.get(dateKey)
    markedDates.set(dateKey, { count: (existing?.count ?? 0) + 1 })
  })

  // 전체 통계 계산
  const stats = calculateStats(executions)

  return (
    <Container maxWidth="lg">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          대시보드
        </Typography>

        <Grid container spacing={3}>
          {/* 캘린더 */}
          <Grid size={{ xs: 12, md: 6 }}>
            <Typography variant="h6" gutterBottom>
              훈련 기록
            </Typography>
            <Calendar markedDates={markedDates} />
          </Grid>

          {/* 전체 통계 */}
          <Grid size={{ xs: 12, md: 6 }}>
            <Typography variant="h6" gutterBottom>
              통계
            </Typography>
            <Paper sx={{ p: 2 }}>
              {executions.length === 0 ? (
                <Typography color="text.secondary">아직 훈련 기록이 없습니다.</Typography>
              ) : (
                <Stack spacing={2}>
                  <StatItem label="총 훈련 횟수" value={`${stats.totalSessions}회`} />
                  <StatItem
                    label="평균 훈련 시간"
                    value={formatDuration(Math.round(stats.avgDuration))}
                  />
                  <StatItem
                    label="단위당 평균 시간"
                    value={formatDuration(Math.round(stats.avgDurationPerUnit))}
                  />
                  <StatItem label="총 훈련 시간" value={formatDuration(stats.totalDuration)} />
                </Stack>
              )}
            </Paper>
          </Grid>

          {/* 훈련 계획별 진행률 */}
          <Grid size={12}>
            <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
              훈련 계획 진행률
            </Typography>
            {plans.length === 0 ? (
              <Alert severity="info">
                훈련 계획이 없습니다.{' '}
                <Button component={Link} to={routes.plans.new.$()} size="small">
                  계획 만들기
                </Button>
              </Alert>
            ) : (
              <Stack spacing={2}>
                {plans.map((plan) => (
                  <PlanProgressCard key={plan.id} plan={plan} />
                ))}
              </Stack>
            )}
          </Grid>
        </Grid>
      </Box>
    </Container>
  )
}

function StatItem({ label, value }: { label: string; value: string }) {
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
      }}
    >
      <Typography color="text.secondary">{label}</Typography>
      <Typography variant="h6">{value}</Typography>
    </Box>
  )
}

function PlanProgressCard({ plan }: { plan: PlanWithProgress }) {
  return (
    <Card>
      <CardContent>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'flex-start',
            mb: 1,
          }}
        >
          <Typography variant="h6">{plan.name}</Typography>
          <Typography variant="body2" color="text.secondary">
            {plan.completedSessions} / {plan.totalSessions}회
          </Typography>
        </Box>
        <LinearProgress
          variant="determinate"
          value={plan.progressPercent}
          sx={{ height: 8, borderRadius: 4, mb: 1 }}
        />
        <Typography variant="body2" color="text.secondary">
          {plan.progressPercent.toFixed(0)}% 완료
        </Typography>
      </CardContent>
      <CardActions>
        <Button
          component={Link}
          to={routes.executions.new.$({ planId: plan.id })}
          startIcon={<PlayIcon />}
          size="small"
        >
          훈련 시작
        </Button>
        <Button component={Link} to={routes.plans.detail.$path({ id: plan.id }, {})} size="small">
          상세보기
        </Button>
      </CardActions>
    </Card>
  )
}
